<template>
  <b-container id = 'app'>
    <b-row>
      <b-col sm="12" md="8" lg="8">
        <div class="square">
          <ul v-for="(obj, index) in test" style="padding: 0;">
            <li :id="index">
              <h1><b># {{index + 1}}</b></h1>
              <div>
                <b-img v-bind:src="obj" fluid />
              </div>
              <p>{{now}}</p>
            </li>
          </ul>
        </div>
      </b-col>
      <b-col sm="0" md="4" lg="4" style="overflow-y: scroll;" class="d-none d-md-block">
        <div class="square" style="overflow-y: scroll;">
          <b-jumbotron container-fluid style="position: fixed; overflow-y: scroll" bg-variant="white" v-bind:header="h1Text">
            <a href="#1">lorem</a><br>
            <a href="#2">lorem</a><br>
            <a href="#3">lorem</a><br>
            <a href="#4">lorem</a><br>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
            <h4>lorem</h4>
          </b-jumbotron>
        </div>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
  let date = new Date()
  export default {
  name: 'HelloWorld',
  data () {
    return {
      now: `${date.getFullYear()}/${date.getMonth()}/${date.getDate()} ${date.getHours()}: ${date.getMinutes()}`,
      msg: 'Welcome to Your Vue.js App',
      text: `
        Quis magna Lorem anim amet ipsum do mollit sit cillum voluptate ex nulla
        tempor. Laborum consequat non elit enim exercitation cillum aliqua
        consequat id aliqua. Esse ex consectetur mollit voluptate est in duis
        laboris ad.
      `,
      h1Text: `
      Quis magna.
      `,
      test: ['https://i.imgur.com/efFTTIT.jpg', 'https://i.imgur.com/AM3lVYo.gif', 'https://i.imgur.com/BwXxYmu.jpg', 'https://i.imgur.com/tZL0CbA.gif'
        , 'https://i.imgur.com/309wjS9.jpg', 'https://i.imgur.com/GynjwN2.jpg', 'https://i.imgur.com/oo4TKze.jpg']
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



